.treasury-top {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 0px;
  height: 120px;
  flex: none;
  order: 1;
  margin-top: 20px;
  border-bottom: 1px solid rgba(102, 104, 106, 0.32);

  .child-boder {
    border-right: 1px solid rgba(102, 104, 106, 0.32);
  }

  .treasury-top-content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0px 64px;
    height: 68px;
    gap: 8px;

    .treasury-top-content-title {
      height: 24px;
      font-family: 'Poppins';
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      color: #29C5F6;
    }

    .treasury-top-content-value {
      font-family: 'Poppins';
      font-style: normal;
      font-weight: 700;
      font-size: 24px;
      line-height: 36px;
      color: white;
    }

    .treasury-top-content-unit {
      color: rgba(255, 255, 255, 0.6);
      font-size: 14px;
    }
  }
}

.modal-title {
  font-weight: 600;
  font-size: 44px;
  line-height: 66px;
  color: #FFFFFF;
  width: 100%;
  text-align: center;
}

.price-content {
  .title {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: #29C5F6;
  }

  .bond-price {
    font-weight: 700;
    font-size: 24px;
    line-height: 36px;
    text-align: center;
    color: #FF00E5;
  }

  .market-price {
    font-weight: 700;
    font-size: 24px;
    line-height: 36px;
    text-align: center;
    color: white;
  }

  .crypto-price {
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    text-align: center;
    color: #465A79;
  }
}

.border {
  border: 1px solid rgba(102, 104, 106, 0.32);
  max-width: 730px;
  margin: auto;
}

.border-right {
  border-right: 1px solid rgba(102, 104, 106, 0.32);
}

.crypto-item {
  text-align: center;

  img {
    width: 100px;
    height: 100px;
  }

  .crypto-name {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: #29C5F6;
  }
}

.clock-content {
  display: flex;
  flex-direction: column;
  gap: 18px;
  align-items: center;

  img {
    width: 34px;
    height: 34px;
  }

  p {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: #29C5F6;
  }

  div {
    background: #0D141E;
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
    text-align: center;
    color: #FFFFFF;
    padding: 7px 23px;
    width: 287px;
    height: 50px;
    border: 1px solid rgba(54, 160, 253, 0.48);
  }
}

.bond-content {
  display: flex;
  justify-content: center;
  gap: 20px;

  input {
    border: 1px solid rgba(54, 160, 253, 0.48);
    background-color: #0D141E;
    color: white;
    text-align: center;
  }

  button {
    background-color: rgba(54, 160, 253, 0.48);
    color: white;
  }

}