@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');

*,
*::before,
*::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Poppins', sans-serif;
}

a {
    color: inherit;
    text-decoration: none;
}

.container {
    margin: 0 auto;
    padding: 0 20px;
    max-width: 1464px;
    width: 100%;
}

.boomerang {
    background: #0D141E;
    overflow: hidden;
}

.boomerang__title {
    margin: 0;

    font-weight: 600;
    font-size: 56px;
    line-height: 84px;
    color: #fff;
}

.boomerang__title--shadowed {
    background: linear-gradient(79.62deg, rgba(255, 255, 255, 0.72) -8.51%, #FFFFFF 35.28%, rgba(169, 198, 215, 0.64) 101.59%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    color: transparent;
    isolation: isolate;
}

.boomerang__title--secondary {
    font-size: 44px;
    line-height: 66px;
}

.boomerang__title--tertiary {
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
}

.boomerang__title--blue {
    color: #29C5F6;
}

.boomerang__title--yellow {
    color: #FFEF64;;
}

.boomerang__caption {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #fff;
}

.boomerang__caption--blue {
    color: #29C5F6;
}

.boomerang__caption--small {
    font-size: 12px;
    line-height: 18px;
}

.boomerang__text {
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
    color: #29C5F6;
}

.boomerang__sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0,0,0,0);
    border: 0;
}

.boomerang__glow {
    position: absolute;
    background: rgba(22, 154, 250, 0.08);
    filter: blur(312px);
    pointer-events: none;
    user-select: none;
}

.boomerang__hero {
    padding: 87px 0 0;
}

.boomerang__features {
    margin: -200px 0 0;
}

.boomerang__response {
    margin-top: 125px;
}

.boomerang__growth {
    margin-top: 230px;
}

.boomerang__sources {
    margin-top: 324px;
}

.boomerang__stracture {
    margin-top: 190px;
}

.boomerang__terms {
    margin-top: 216px;
}

.boomerang__footer {
    margin-top: 220px;;
}

.boomerang-header {
    position: relative;
}

.boomerang-header__content {
    display: flex;    
    border-bottom: 1px solid rgba(102, 104, 106, 0.32);
    padding: 26px 0;
    display: flex;
    align-items: center;
}

.boomerang-header__logo {
    display: flex;
    width: 229px;
    height: 54px;
    margin-right: 262px;
    flex-shrink: 0;
    transition: opacity 0.25s ease;
}

.boomerang-header__logo:hover {
    opacity: 0.8;
}

.boomerang-header__logo:active {
    opacity: 0.7;
}

.boomerang-header__logo img {
    width: 100%;
    height: 100%;
    max-width: 100%;
    object-fit: cover;
}

.boomerang-header__links {
    list-style: none;
    padding: 0;
    display: flex;
}

.boomerang-header__link {
    display: flex;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #fff;
    transition: opacity 0.25s ease;
}

.boomerang-header__link a {
    padding: 26px 42px;
}

.boomerang-header__link:hover {
    opacity: 0.8;
}

.boomerang-header__link:active {
    opacity: 0.7;
}

.boomerang-header__right {
    display: flex;
    align-items: center;
    width: 100%;
}

.boomerang-header__right--mobile {
    display: flex;
    justify-content: flex-end;

    display: none;
}

.boomerang-header__right--mobile .boomerang-header__langs {
    margin: 0 10px;
    margin-left: 0;
}

.boomerang-header__right--mobile-curtain .boomerang-header__socials {
    display: none;
}

.boomerang-hamburger {
    border: none;
    width: 44px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: none;
    transition: opacity 0.25s ease;
    cursor: pointer;
}

.boomerang-hamburger:hover {
    opacity: 0.8;
}

.boomerang-hamburger:active {
    opacity: 0.7;
}

.boomerang-hamburger--open .boomerang-hamburger__strip {
    margin: 0;
}

.boomerang-hamburger--open .boomerang-hamburger__strip:nth-child(2) {
    display: none;
}

.boomerang-hamburger--open .boomerang-hamburger__strip:nth-child(1) {
    transform: translateY(7px) rotate(45deg);
}

.boomerang-hamburger--open .boomerang-hamburger__strip:nth-child(3) {
    transform: translateY(4px) rotate(-45deg);
}

.boomerang-hamburger__strips {
    position: relative;
    width: 22px;
    height: 18px;
}

.boomerang-hamburger__strip {
    display: block;
    margin-bottom: 4px;
    width: 100%;
    height: 3px;
    background: #fff;
    transition: transform 0.25s ease;
}

.boomerang-hamburger__strip:last-of-type {
    margin-bottom: 0;
}



/* .boomerang-header__right .boomerang-header__socials {
    display: none;
} */

.boomerang-header__langs {
    margin-left: auto;
}

.boomerang-lang:focus-within .boomerang-lang__list {
