.boomerang-hero__controls::after {
    content: "";
    position: absolute;
    top: 22px;
    left: 0;
    width: 169px;
    height: 33px;
    background: rgba(41, 197, 246, 0.35);
    filter: blur(128px);
}

.boomerang-hero__controls::before {
    content: "";
