}


.boomerang-hero__btn {
    position: relative;
    z-index: 1;
    display: flex;
    border: none;
    background: none;
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    letter-spacing: -0.03em;
    color: #fff;
    cursor: pointer;
    transition: transform 0.25s ease, opacity 0.25s ease;
}

.boomerang-hero__btn span {
    position: relative;
    z-index: 10;

    border-radius: 16px 4px 4px 4px;
    padding: 24px 64px;
    height: 100%;
    background: radial-gradient(66.1% 66.1% at 50% 17.8%, #29C5F6 0%, #1C8ADE 100%), #29C5F6;
    cursor: pointer;
}

.boomerang-hero__btn::after {
    content: "";
    position: absolute;
    z-index: 2;
    top: -1px;
    left: -1px;
    right: -1px;
    bottom: -1px;
    background: linear-gradient(to bottom, #4BD4FF, #017BD5);
    border-radius: 16px 4px 4px 4px;
    cursor: pointer;
}

.boomerang-hero__btn--pressable::before {
    content: "";
    position: absolute;
    bottom: -4px;
    left: -1px;
    width: calc(100% + 2px);
    height: 6px;
    background: #004E86;
    z-index: 1;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    cursor: pointer;
    transition: transform 0.25s ease;
}

.boomerang-hero__btn--pressable:hover span {
    background: linear-gradient(180deg, #4BD4FF 0%, #007AD5 100%);
}

.boomerang-hero__btn--pressable:active span {
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), linear-gradient(180deg, #4BD4FF 0%, #007AD5 100%);
}

.boomerang-hero__btn--pressable:active::before {
    background: #004E86;
}

.boomerang-hero__btn--dark {
    margin-left: 30px;
}

.boomerang-hero__btn--dark:hover::after {
    background: #29C5F6;
}
.boomerang-hero__btn--dark:hover span {
    background: linear-gradient(85deg, #151B26 , #101621 6%,  #111E2A);
}

.boomerang-hero__btn--dark:active::after {
    background: #0096C5;
}

.boomerang-hero__btn--dark:active span {
    background: #0D141E;
}

.boomerang-hero__btn--discord:hover::after {
    background: #29C5F6;
}
.boomerang-hero__btn--discord:hover span {
    background: linear-gradient(85deg, #151B26 , #101621 6%,  #111E2A);
}

.boomerang-hero__btn--discord:active::after {
    background: #0096C5;
}

.boomerang-hero__btn--discord:active span {
    background: #0D141E;
}


.boomerang-hero__btn--dark span  {
    position: relative;
    z-index: 1000;
    background: linear-gradient(85deg, #151B26 , #101721 6%,  #171d28);
}

.boomerang-hero__btn--dark::after {
    background: linear-gradient(47deg, rgba(255, 255, 255, 0) 30%, #1E2933 40%, #2E3E4C 80%, #587A95 90%, #58778d);
}

.boomerang-hero__btn--discord span {
    position: relative;
