.dashboard-view {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  overflow: hidden;
  width: 88%;
  margin: 50px 90px;

  @media screen and (max-width: 960px) {
    width: 100%;
    margin: 0px;
    padding: 0px 4px;
  }

  .dashboard-infos-wrap {
    .dashboard-head {
      font-family: 'Poppins';
      font-style: normal;
      font-weight: 400;
      font-size: 20px;
      line-height: 23px;
      letter-spacing: 0.3em;
      text-transform: uppercase;
      letter-spacing: 0.3em;
      color: white;
    }

    .dashboard-top-content {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      border-radius: 10px;
      border: 1px solid rgba(54, 160, 253, 0.48);

      .dashboard-top-content-title {
        height: 24px;
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        color: #29C5F6;
      }

      .dashboard-top-content-value {
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 700;
        font-size: 24px;
        line-height: 36px;
        color: white;
      }

      .dashboard-top-content-unit {
        color: rgba(255, 255, 255, 0.6);
        font-size: 14px;
      }

      .line-container {
        display: flex;
        flex-direction: row;
        padding: 0px 16px;
        background: linear-gradient(269.39deg, rgba(25, 44, 61, 0.48) 3.53%, rgba(14, 44, 71, 0.48) 101.76%);
        height: 72px;
        width: 100%;
        align-items: center;
        justify-content: space-between;
      }

      .title {
        color: #29C5F6;
      }

      .value {
        color: white;
      }

      .odd-background {
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), linear-gradient(269.39deg, rgba(25, 44, 61, 0.48) 3.53%, rgba(14, 44, 71, 0.48) 101.76%);
      }
    }

    .dashboard-card {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;

      position: relative;
      z-index: 2;

      @supports (-webkit-backdrop-filter: none) or (backdrop-filter: none) {
        background: rgba(0, 0, 0, 0.2);
        backdrop-filter: blur(100px);
      }

      /* slightly transparent fallback for Firefox (not supporting backdrop-filter) */
      @supports not ((-webkit-backdrop-filter: none) or (backdrop-filter: none)) {
        background: #181B1C;
      }

      border-radius: 10px;
      padding: 13px 0;

      .card-title {
        font-family: Poppins;
        font-weight: 500;
        font-size: 20px;
        color: rgba(255, 255, 255, 0.6);
      }

      .card-value {
        font-family: Poppins;
        font-weight: bold;
        font-size: 24px;
        font-weight: bold;
        color: #FFFFFF;
      }
    }

    .dashboard-container {
      margin-top: 40px;
    }
  }
}