.mobile-dapp-sidebar {
  display: flex;
  flex-direction: column;
  background-color: rgba(13, 13, 13, 1);
  border-top: 1px solid #29C5F6;

  .dapp-menu-links {
    display: flex;
    align-items: center;
    justify-content: space-around;

    .dapp-nav {
      display: flex;
      justify-content: space-around;

      a {
        margin-top: 20px;
        margin-left: 20px;
        align-items: center;
        margin-bottom: 20px;

        img {
          width: 20px;
          height: 20px;
          margin-right: 10px;
          vertical-align: -4px;
        }
      }

      .button-dapp-menu {
        .dapp-menu-item {
          display: flex;
          flex-direction: row;
          align-items: center;
          color: #29C5F6;

          p {
            font-weight: 600;
            font-size: 18px;
            font-family: "Poppins";
            font-style: normal;
            font-weight: 700;
            letter-spacing: -0.03em;
          }
        }
      }

      .button-dapp-menu.active {
        .dapp-menu-item {
          p {
            color: #FFFFFF;
          }

          img {
            fill: #29C5F6;
          }
        }
      }

      .bond-discounts {
        text-align: left;
        padding-left: 54px;

        a {
          margin-left: 0;
          margin-top: 20px;
          margin-bottom: 20px;
        }

        p {
          font-family: Poppins;
          font-size: 14px;
          line-height: 17px;
          color: rgba(255, 255, 255, 0.6);
        }

        .bond {
          display: block;
          padding: unset;
          margin-bottom: 0 0 1px 0;
          text-decoration: none !important;

          .bond-pair-roi {
            float: right;
            margin-left: 33px;
          }
        }
      }
    }
  }

  .dapp-menu-doc-link {
    margin-top: auto;

    a {
      display: flex;
      width: 100%;
      align-items: center;
      justify-content: center;
      margin-bottom: 20px;

      img {
        width: 20px;
        height: 20px;
        margin-right: 10px;
        vertical-align: middle;
      }

      p {
        font-family: Poppins;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        color: #FFFFFF;
      }
    }
  }

  .social-row {
    display: flex;
    flex-flow: row;
    justify-content: space-evenly;
    padding: 1.3rem;

    a {
      &:hover {
        transform: scale(1.1);
      }

      img {
        height: 20px;
        width: 20px;
      }
    }
  }
}

.MuiDrawer-root {
  .MuiPaper-root {

    z-index: 7;

    @supports (-webkit-backdrop-filter: none) or (backdrop-filter: none) {
      background: rgba(36, 39, 41, 0.1);
      backdrop-filter: blur(40px);
    }

    /* slightly transparent fallback for Firefox (not supporting backdrop-filter) */
    @supports not ((-webkit-backdrop-filter: none) or (backdrop-filter: none)) {
      background: #181B1C;
    }
  }
}