.portfolio-view {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  overflow: hidden;
  width: 89%;
  margin: 50px 90px;

  @media screen and (max-width: 960px) {
    width: 100%;
    margin: 0px;
  }

  .portfolio-infos-wrap {
    .portfolio-head {
      font-family: 'Poppins';
      font-style: normal;
      font-weight: 400;
      font-size: 20px;
      line-height: 23px;
      letter-spacing: 0.3em;
      text-transform: uppercase;
      color: white;
    }

    .portfolio-top {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      padding: 0px;
      height: 120px;
      flex: none;
      order: 1;
      margin-top: 20px;
      border-bottom: 1px solid rgba(102, 104, 106, 0.32);

      .child-boder {
        border-right: 1px solid rgba(102, 104, 106, 0.32);
      }

      .portfolio-top-content {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: 0px 64px;
        height: 68px;
        gap: 8px;

        .portfolio-top-content-title {
          height: 24px;
          font-family: 'Poppins';
          font-style: normal;
          font-weight: 400;
          font-size: 16px;
          line-height: 24px;
          color: #29C5F6;
        }

        .portfolio-top-content-value {
          height: 24px;
          font-family: 'Poppins';
          font-style: normal;
          font-weight: 700;
          font-size: 24px;
          line-height: 36px;
          color: white;
        }

        .portfolio-top-content-unit {
          color: rgba(255, 255, 255, 0.6);
          font-size: 14px;
        }
      }
    }

    .portfolio-card {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;

      position: relative;
      z-index: 2;

      @supports (-webkit-backdrop-filter: none) or (backdrop-filter: none) {
        background: rgba(0, 0, 0, 0.2);
        backdrop-filter: blur(100px);
      }

      /* slightly transparent fallback for Firefox (not supporting backdrop-filter) */
      @supports not ((-webkit-backdrop-filter: none) or (backdrop-filter: none)) {
        background: #181B1C;
      }

      border-radius: 10px;
      padding: 13px 0;

      .card-title {
        font-family: Poppins;
        font-weight: 500;
        font-size: 20px;
        color: rgba(255, 255, 255, 0.6);
      }

      .card-value {
        font-family: Poppins;
        font-weight: bold;
        font-size: 24px;
        font-weight: bold;
        color: #FFFFFF;
      }
    }

    .portfolio-container {
      margin-top: 40px;
    }
  }
}