.dapp-topbar {
  width: 89%;
  margin-right: 40px;
  align-items: center;

  .dapp-topbar-slider-btn {
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }

  .dapp-topbar-btns-wrap {
    display: flex;
    align-items: center;
    margin-left: auto;
  }
}

@media screen and (min-width: 960px) {
  .dapp-topbar {
    .dapp-topbar-slider-btn {
      display: none;
    }
  }
}