.card-container {
    display: flex;
    font-family: "Poppins";
    flex-direction: column;
    align-items: flex-start;
    padding: 0px;
    min-height: 120px;
    color: white;
    border-radius: 10px;
    border: 1px solid rgba(54, 160, 253, 0.48);

    .header {
        width: 100%;

        .container {
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;

            .title {
                color: rgba(41, 197, 246, 1);
                font-size: 14px;
            }

            .value {
                color: white;
                font-size: 24px;
                font-family: 'Poppins';
                font-style: normal;
                font-weight: 700;
                font-size: 24px;
                line-height: 36px;
            }

            .unit {
                color: #acacac;
                font-size: 18px;
                font-family: 'Poppins';
                font-style: normal;
                font-weight: 500;
                font-size: 18px;
                line-height: 36px;
            }
        }


    }

    .even-background {
        background: linear-gradient(rgba(25, 44, 61, 0.48),
                rgba(14, 44, 71, 0.48));
    }

    .odd-background {
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), linear-gradient(269.39deg, rgba(25, 44, 61, 0.48) 3.53%, rgba(14, 44, 71, 0.48) 101.76%);
    }

    .line-container {
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: space-between;
        padding: 0px 20px;
        height: 46px;
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 18px;
        position: relative;

        .title {
            color: rgba(41, 197, 246, 1);
        }

        .value {
            color: white
        }

        .button {
            border-radius: 10px;
            background: rgb(41, 197, 246);
            border: none;
            cursor: pointer;
            right: 20px;
            position: absolute;
        }
    }

    .infocard {
        display: flex;
        margin-top: 20px;
        margin-left: 64px;
        flex-direction: column;
        font-family: "poppins";

        .title {
            color: rgba(41, 197, 246, 1);
            font-weight: 400;
            font-size: 16px;
        }

        .value-container {
            display: flex;
            color: white;
            align-items: flex-end;
            margin-top: 12px;

            .value {
                font-size: 24px;
            }

            .unit {
                font-size: 16px;
                margin-left: 4px;
                color: rgba(255, 255, 255, 0.6);
            }
        }
    }
}