.view-base-root {
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    background: #0D141E;

    .cubes-top {
        opacity: 0.6;
        position: absolute;
        top: -120px;

        p {
            font-family: Poppins;
            font-style: normal;
            font-weight: normal;
            font-size: 6px;
            color: #FFFFFF;
            white-space: pre-wrap;
            user-select: none;
        }
    }


    .cubes-bottom {
        opacity: 0.6;
        position: absolute;
        bottom: -256px;
        right: -140px;

        p {
            font-family: Poppins;
            font-style: normal;
            font-weight: normal;
            font-size: 12px;
            color: #FFFFFF;
            white-space: pre-wrap;
            user-select: none;
        }
    }

    .wallet-link {
        margin: 10px;
        display: flex;
        align-items: center;
        column-gap: 4px;
        margin-right: 14px;

        @media screen and (max-width: 960px) {
            margin: 10px 4px;
        }

        p {
            font-family: Poppins;
            font-size: 16px;
            color: #FFFFFF;

            @media screen and (max-width: 960px) {
                font-size: 14px;
            }
        }
    }

    .main-background {
        position: absolute;
        width: 1019px;
        height: 1019px;
        left: 901px;
        top: -49px;
        background: rgba(22, 154, 250, 0.1);
        filter: blur(512px);
    }
}